/**
@import "../common/mixins";
@import "../common/variable";
 */

.baker {
  background-color: #f8f8f8;
  padding-bottom: 70px;
  &__switcher-buttons {
    max-width: 1920px;
    margin-inline: auto;
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    button, .btn-switch {
      width: 100%;
      -webkit-box-flex: 1;
          -ms-flex: 1;
              flex: 1;
    }
  }
  &__content {
    padding: 72px 0;
  }
  &__swiper {
    .swiper {
      .swiper-wrapper {
        .swiper-slide {}
      }
    }
  }
}

.hero + .baker {
  margin-top: 35px;
}

.baker-card {
  position: relative;
  border-radius: 5px;
  -webkit-box-shadow: 0 4px 10px 0 rgba(0, 0, 0, 0.1);
          box-shadow: 0 4px 10px 0 rgba(0, 0, 0, 0.1);
  -webkit-transition: .3s;
  transition: .3s;
  &__picture {
    position: relative;
    background-color: grey;
    a {}
    picture {}
    img {}
  }
  &__discount {
    position: absolute;
    top: 0;
    left: 0;
    z-index: 2;
    border-radius: 5px 0 5px 0;
  }
  &__fav {
    position: absolute;
    top: 25px;
    right: 22px;
    z-index: 2;
  }
}